<template>
	<view class="content">
		<view class="cu-form-group">
			<text class="tit">收支类型</text>
			<picker @change="bindPickerType" :value="details.type" :range="pickerType">
				<view class="picker">
					{{pickerType[details.type]}}
				</view>
			</picker>
		</view>
		<view class="cu-form-group">
			<view class="title">金额</view>
			<input maxlength="11" class="text-right" type="number" placeholder="金额" v-model="details.money" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group align-start">
			<view class="title">说明</view>
			<textarea maxlength="200" v-model="details.remark" placeholder="收支说明"></textarea>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Finance from '../../api/finance'
	import { objectURLToBlob } from '../../utils'
	export default {
		data() {
			return {
				pickerType: ['收入','支出','优惠'],
				id: '',
				oid: '',
				details: {
					type: 0,
					money: '',
					remark: '',
				}
			}
		},
		onLoad(option){
			this.oid = option.oid
			this.details.good_indent_id = this.oid
			this.id = option.id
			this.index = option.index
			this.getList()
		},
		methods: {
			async getList(){
				let title = '新增收支配置'
				const that = this
				if(this.id > 0){
					title = '编辑收支配置'
					Finance.getDetails(this.id,{},function(res){
						that.details = res
					})
				}
				uni.setNavigationBarTitle({
					title
				})
			},
			
			//提交
			confirm(){
				let data = this.details
				const that = this
				if(!data.money){
					this.$api.msg('请填写金额');
					return;
				}
				if(!(/(^[1-9]\d*$)/.test(data.money))){
					this.$api.msg('金额只能是正整数');
					return;
				}
				if(!data.remark){
					this.$api.msg('请填写说明');
					return;
				}
				
				if(data.id){
					Finance.updateSubmit(data,function(res){
						that.$api.prePage().refreshFinanceList(that.index, data)
						that.$api.msg(`收支配置修改成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}else{
					Finance.createSubmit(data,function(res){
						data.id = res
						that.$api.prePage().refreshFinanceList(that.index, data)
						that.$api.msg(`收支配置添加成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}
			},
			// 类型
			bindPickerType(e) {
				this.details.type = e.detail.value
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}

	.row{
		display: flex;
		align-items: center;
		position: relative;
		padding:0 30upx;
		height: 110upx;
		background: #fff;
		
		.tit{
			flex-shrink: 0;
			width: 120upx;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.input{
			flex: 1;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.icon-shouhuodizhi{
			font-size: 36upx;
			color: $font-color-light;
		}
	}
	.default-row{
		margin-top: 16upx;
		.tit{
			flex: 1;
		}
		switch{
			transform: translateX(16upx) scale(.9);
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
	
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
	
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
</style>
